import React from 'react'
import { SRLWrapper } from 'simple-react-lightbox'
import Vito from '../../shared/images/vito.jpg'
import Hyundai from '../../shared/images/ostrava-view-bus.jpg'
import Side from '../../shared/images/side.jpg'
import Backside from '../../shared/images/backside.jpeg'
import Mercedes from '../../shared/images/mikrobus_black.jpg'
import './lightboxcomponent.css'

const options = {
    settings: {
        overlayColor: 'rgba(0, 0, 0, 0.9)',
        autoplaySpeed: 3000,
        transitionSpeed: 600,
    },
    buttons: {
        showAutoplayButton: false,
        showDownloadButton: false,
    },
    caption: {
        showCaption: true,
        captionColor: '#fff'
    }
}

export const LightboxComponent = () => {
    return (
        <SRLWrapper options={options}>
            <div className='container-md light' id='fotogalerie'>
                <h1>Fotogalerie</h1>
                <p className="gallery-subtitle">Prohldnte si n modern vozov park</p>
                
                <div className='row gallery-row'>
                    <div className='col-sm gallery-item'>
                        <div className="image-container">
                            <img src={Vito} alt='Mercedes Vito - pedn pohled' 
                                 data-caption="Mercedes Vito - modern a pohodln vz pro vai pepravu" />
                            <div className="image-overlay">
                                <span>Mercedes Vito</span>
                            </div>
                        </div>
                    </div>
                    <div className='col-sm gallery-item'>
                        <div className="image-container">
                            <img src={Backside} alt='Prostorn zavazadlov prostor' 
                                 data-caption="Prostorn zavazadlov prostor pro vae zavazadla" />
                            <div className="image-overlay">
                                <span>Zavazadlov prostor</span>
                            </div>
                        </div>
                    </div>
                </div>

                <div className='row gallery-row'>
                    <div className='col-sm gallery-item'>
                        <div className="image-container">
                            <img src={Side} alt='Komfortn interir vozu' 
                                 data-caption="Komfortn interir pro pohodln cestovn" />
                            <div className="image-overlay">
                                <span>Interir vozu</span>
                            </div>
                        </div>
                    </div>
                    <div className='col-sm gallery-item'>
                        <div className="image-container">
                            <img src={Hyundai} alt='Bon pohled na vz' 
                                 data-caption="Elegantn design naeho vozu" />
                            <div className="image-overlay">
                                <span>Bon pohled</span>
                            </div>
                        </div>
                    </div>
                </div>

                <div className='row gallery-row'>
                    <div className='col-sm gallery-item'>
                        <div className="image-container">
                            <img src={Mercedes} alt='Mercedes tda V - luxusn peprava' 
                                 data-caption="Mercedes tda V - luxus a komfort pro vae cesty" />
                            <div className="image-overlay">
                                <span>Mercedes tda V</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </SRLWrapper>
    )
}